import helpers from '@/helpers/global';
import BaseFooterSimple from '@/components/BaseFooterSimple.vue';
import BasePagination from '@/components/UI/Pagination/BasePagination';
import ModalBooking from '@/components/UI/Modal/ModalBooking';
import FilterRange from '@/components/FilterRange';
import FilterCheckbox from '@/components/FilterCheckbox';
import FilterButton from '@/components/FilterButton';
import BaseBanner from '@/components/BaseBanner';
import FilterCheckboxModels from '@/components/FilterCheckboxModels';

export default {
    components: {
        ModalBooking,
        BaseFooterSimple,
        FilterRange,
        FilterCheckbox,
        FilterButton,
        BaseBanner,
        FilterCheckboxModels,
        BasePagination,
    },
    data() {
        return {
            showModal: false,
        };
    },
    props: {
        product: {
            type: Object,
        },
    },
    emits: ['selectedCar'],
    methods: {
        selectCar(brand, model) {
            this.$emit('selectedCar', brand + ' ' + model);
        },

        getClass(item) {
            return helpers.checkLabelProduct(item);
        },
    },
    computed: {
        modelName() {
            return helpers.removeBrandReplay(
                this.product.model,
                this.product.brand
            );
        },
    },
};
